import { combineReducers } from "redux";

const title = (state = "", action) => {
  switch (action.type) {
    case "INPUT_TITLE":
      return action.text;
    case "START_LIVE":
      return action.title;
    case "SUBSCRIBE_CHANNEL":
      return action.title;
    default:
      return state;
  }
};

const channel = (state = "", action) => {
  switch (action.type) {
    case "INPUT_CHANNEL":
      return action.text;
    case "START_LIVE":
      return action.channel;
    case "SUBSCRIBE_CHANNEL":
      return action.channel;
    default:
      return state;
  }
};

const name = (state = "Anonymous🤡", action) => {
  switch (action.type) {
    case "INPUT_NAME":
      return action.text;
    default:
      return state;
  }
};

const message = (state = "", action) => {
  switch (action.type) {
    case "FETCH_CHANNEL":
      return "Connecting Channel...";
    case "SUCCESS_FETCH":
      return "";
    case "ERROR_FETCH":
      return "Not find your Channel.";
    case "INPUT_CHANNEL":
      return "";
    default:
      return state;
  }
};

const myvoice = (state = "", action) => {
  switch (action.type) {
    case "INPUT_VOICE":
      return action.text;
    case "EMIT_VOICE":
      return "";
    default:
      return state;
  }
};

const voices = (state = [], action) => {
  switch (action.type) {
    case "RECEIVE_VOICE":
      let new_state = state;
      if (action.voice.content !== "") {
        new_state = state.concat(action.voice);
      }
      return new_state;
    default:
      return state;
  }
};

const fetchingPerformer = (state = false, action) => {
  switch (action.type) {
    case "FETCH_LIVE":
      return true;
    case "SUCCESS_FETCH":
      return false;
    case "ERROR_FETCH":
      return false;
    default:
      return state;
  }
};

const fetchingListener = (state = false, action) => {
  switch (action.type) {
    case "FETCH_CHANNEL":
      return true;
    case "SUCCESS_FETCH":
      return false;
    case "ERROR_FETCH":
      return false;
    default:
      return state;
  }
};

export default combineReducers({
  title,
  channel,
  name,
  message,
  myvoice,
  voices,
  fetchingPerformer,
  fetchingListener
});
