export const inputTitle = text => ({
  type: "INPUT_TITLE",
  text
});

export const inputChannel = text => ({
  type: "INPUT_CHANNEL",
  text
});

export const inputName = text => ({
  type: "INPUT_NAME",
  text
});

export const inputVoice = text => ({
  type: "INPUT_VOICE",
  text
});

export const receiveVoice = voice => ({
  type: "RECEIVE_VOICE",
  voice
});

export const emitVoice = voice => ({
  type: "EMIT_VOICE",
  voice
});

const fetchLive = () => ({
  type: "FETCH_LIVE"
});

const fetchChannel = () => ({
  type: "FETCH_CHANNEL"
});

const successFetch = () => ({
  type: "SUCCESS_FETCH"
});

const errorFetch = () => ({
  type: "ERROR_FETCH"
});

const startLive = (title, channel) => ({
  type: "START_LIVE",
  title,
  channel
});

const subscribeChannel = (title, channel, voices) => ({
  type: "SUBSCRIBE_CHANNEL",
  title,
  channel,
  voices
});

//非同期アクション
const API_PATH = "http://localhost:9000";

export const createLive = (title, history) => dispatch => {
  dispatch(fetchLive());
  fetch(API_PATH + `/new_channel?title=${title}`, {
    method: "POST"
  })
    .then(res => {
      return res.json();
    })
    .then(result => {
      dispatch(startLive(result.title, result.channel));
      history.push("/board");
      dispatch(successFetch());
    })
    .catch(err => {
      dispatch(errorFetch());
      //alert(err.message)
    });
};

export const findChannel = (channel, history) => dispatch => {
  dispatch(fetchChannel());
  fetch(API_PATH + `/check_channel?channel=${channel}`, {
    method: "GET"
  })
    .then(res => {
      return res.json();
    })
    .then(result => {
      console.log(result.voices);
      dispatch(subscribeChannel(result.title, result.channel, result.voices));
      history.push("/board");
      dispatch(successFetch());
    })
    .catch(err => {
      dispatch(errorFetch());
      //alert(err.message)
    });
};
